<template>
    <div id="changePassword">
        <mt-header title="修改密码" style="height:.88rem;background-color:#4a57ff">
            <mt-button icon="back" slot='left' @click="goBack"></mt-button>  
        </mt-header>
        <div class="content">
            <div class="item_input">
                <div class="laber">手机号码</div>
                <input type="text" v-model="phone" disabled>
                <div class="sendYzm" @click="sendYzm"><span v-if='sendState==1'>发送验证码</span><span v-if='sendState==2'>{{num}}s</span><span v-if='sendState==3'>重新获取</span></div>
            </div>
            <div class="item_input">
                <div class="laber">验证码</div>
                <input type="text" v-model="code" placeholder="请输入验证码">
            </div>
            <mt-button @click="submit" class="submit" type="primary" :disabled="code?false:true">确定</mt-button>
        </div>
    </div>
</template>
<script>
import { getVerifyCode, checkVerifyCode } from '@/api/withdraw.js'
import { getPhone } from '@/utils/native.js'
import { Toast } from 'mint-ui';
export default {
    data() {
        return {
            phone:'151 **** 2530', // 电话
            sendState: 1, // 发送验证码状态值
            code:'', // 验证码
            num:60 , // 倒计时值
            intervalId:'', // 计时器id
            temp_token:''
        }
    },
    created() { 
        this.phone = this.tokus.phone 
    },
    methods:{
        goBack() {
            this.$router.go(-1)
        },
        submit() {
            let verify_code = this.code
            checkVerifyCode({verify_code}).then(data=>{
                if(data.code == 0) {
                    this.temp_token = data.data.temp_token
                    this.$router.push({ path:`/setPassword/${this.temp_token}`})
                }else {
                    Toast(data.message)
                }
            })
            
        },
        sendYzm(num) {
            if(num==2){
                return false
            }else{
                getVerifyCode({mobile:this.phone}).then(data => {
                    if(data.code ===0){
                        Toast('发送成功')
                    }else{
                       Toast(data.message)
                    }
                })
                this.sendState = 2
                this.intervalId = setInterval(()=>{
                    if(this.num > 0){
                        this.num --
                    }else{
                        clearInterval(this.intervalId) // 清楚当前计时器，以免损耗内存
                        this.sendState = 3 
                        this.num = 60
                    }
                    
                },1000)
            }
        }
    }
}
</script>
<style lang="less" scoped>
    #changePassword{
        width: 100%;
        height: 13.34rem;
        background-color: #fafafa;
        .content{
            width: 100%;
            height: auto;
            padding-top: .4rem;
            .submit{
                width: 6.9rem;
                height: .8rem;
                margin: 2.2rem auto 0;
                border-radius: .1rem;
                display: block;
                background-color: rgb(74, 87, 255);
                font-size: .34rem;
            }
            .item_input{
                width: 100%;
                height: 1rem;
                background-color: #fff;
                display: flex;
                justify-content: space-between;
                align-items: center;
                position: relative;
                .laber{
                    width: 2.04rem;
                    height: 100%;
                    line-height: 1rem;
                    font-family: MicrosoftYaHei;
                    font-size: .3rem;
                    font-weight: normal;
                    font-stretch: normal;
                    letter-spacing: 0px;
                    color: #000000;
                    padding-left: .3rem;
                }
                .sendYzm{
                    position: absolute;
                    width: 1.8rem;
                    height: .6rem;
                    background-color: #4a57ff;
                    border-radius: .1rem;
                    line-height: .64rem;
                    text-align: center;
                    font-family: MicrosoftYaHei;
                    font-size: .28rem;
                    font-weight: normal;
                    font-stretch: normal;
                    letter-spacing: 0px;
                    color: #ffffff;
                    top: 50%;
                    right: .3rem;
                    transform: translateY(-50%);
                }
                input{
                    width: 100%;
                    height: 100%;
                    border:none;
                    font-family: MicrosoftYaHei;
                    font-size: .3rem;
                    font-weight: normal;
                    font-stretch: normal;
                    letter-spacing: 0px;
                    color: #666666;
                }
            }
            .item_input:nth-of-type(1) {
                border-bottom: 1px solid #e5e5e5;
            }
        }
    }
</style>



